                    { property: 'og:title', content: "İstanbul Kadınlar ve Erkekler"},
                    { property: 'og:site_name', content: 'VIP PRO'},
                    {property: 'og:type', content: 'website'},    
                    { name: 'robots', content: 'index,follow'} 
                ]
            }
        },
        data() {
            return {
              type: '',
              ilce: '',
              yas: '',
              products: []
            }
        },
        computed: {
            filterProducts: function(){
                return this.filterProductsByType(this.filterProductsByCategory(this.filterProductsByAge(this.$store.state.users)))
            }
        },
        methods: {
            filterProductsByType: function(products=this.$store.state.users){
                return products.filter(product => !product.type.indexOf(this.type))
            },
            filterProductsByCategory: function(products=this.$store.state.users){
                return products.filter(product => !product.ilce.indexOf(this.ilce))
            },
            filterProductsByAge: function(products=this.$store.state.users){
                return products.filter(product => !product.yas.indexOf(this.yas))
            }
        },
}
</script>